import { WebPlugin } from '@capacitor/core';

import type { ImageGalleryPlugin, InitConfig, PickOptions, PickResult } from './definitions';

export class ImageGalleryPluginWeb extends WebPlugin implements ImageGalleryPlugin {
  async initialize(config?: InitConfig): Promise<void> {
    console.log('ImageGallery.initialize() called on web', config);
    // Web implementation not supported
    throw new Error('ImageGallery plugin is not supported on web platform');
  }

  async pick(options?: PickOptions): Promise<PickResult> {
    console.log('ImageGallery.pick() called on web', options);
    // Web implementation not supported
    throw new Error('ImageGallery plugin is not supported on web platform');
  }
}
